
ul,
li {
    list-style: none;
    padding-left: 0;
}

p {
    margin: 0;
}


.audioWrapper {
    width: 90vw;
    height: 100vh;
    margin: 0 auto;
    background-color: #000;
    color: #fff;
}

.lyric-wrapper {
    position: relative;
    width: 90vw;
    height: calc(100vh - 60px);
    margin: 0 auto;

    .scroll-content {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        margin: 10px auto;
        text-align: center;
        transition: top 1s linear;

        li {
            line-height: 30px;
            opacity: 0.6;
            transition: transform 0.5s linear, opacity 0.4s;
        }

        li.active {
            transform: scale(1.2);
            opacity: 1;
        }
    }
}
